<template>
  <v-container grid-list-md>
    <div>

      <section class="container">

        <header> नोंदणी पत्रक</header>
        <form class="form">
          <div class="column">
            <div class="input-box">
              <label>पहिले नाव</label>
              <input type="text" placeholder="पहिले नाव" required />
            </div>
            <div class="input-box">
              <label>मधले नाव</label>
              <input type="text" placeholder="मधले नाव" required />
            </div>
            <div class="input-box">
              <label>आडनाव</label>
              <input type="text" placeholder="आडनाव" required />
            </div>
          </div>
          <br>
          <div class="column">
            <div class="input-box">
              <label>ईमेल </label>
              <input type="text" placeholder="ईमेल" required />
            </div>
          </div>
          <br>

          <div class="column">
            <div class="input-box">
              <label>दूरध्वनी क्रमांक</label>
              <input type="number" v-model="regisration.mobileno" placeholder="दूरध्वनी क्रमांक" required />
            </div>
            <div class="input-box">
              <label>जन्मदिनांक</label>
              <input type="date" placeholder="जन्मदिनांक" required />
            </div>
          </div>
          <br>
          <div class="gender-box">
            <h3>लिंग</h3>
            <div class="gender-option">
              <div class="gender">
                <input type="radio" id="check-male" name="gender" />
                <label for="check-male">पुरुष</label>
              </div>
              <div class="gender">
                <input type="radio" id="check-female" name="gender" />
                <label for="check-female">स्त्री</label>
              </div>
              <div class="gender">
                <input type="radio" id="check-other" name="gender" />
                <label for="check-other">इतर</label>
              </div>
            </div>
          </div>

          <div class="input-box address">
            <label>मतदानाचा पत्ता</label>
            <!-- <input type="text" placeholder="Enter street address" required />
          <input type="text" placeholder="Enter street address line 2" required /> -->
            <div class="column">
              <div class="select-box">
                <select>
                  <option hidden>जिल्हा</option>
                  <option>पुणे</option>

                </select>
              </div>

              <div class="select-box">
                <select>
                  <option hidden>विधान सभा क्षेत्रों</option>
                  <option>पुणे</option>
                  <option>बारामती</option>
                  <option>अहमदनगर</option>

                </select>
              </div>
              <div class="select-box">
                <select>
                  <option hidden>तहशील</option>
                  <option>खेड़</option>
                  <option>बारामती</option>
                  <option>भोर</option>

                </select>
              </div>
            </div>
          </div>
          <div class="gender-box">
            <h3>क्षेत्र</h3>
            <div class="gender-option">
              <div class="area">
                <input type="radio" id="check-city" name="area" />
                <label for="check-city">शहर</label>
              </div>
              <div class="gender">
                <input type="radio" id="check-village" name="area" />
                <label for="check-village">गाव</label>
              </div>
            </div>
          </div>
          <div class="column">
            <div class="input-box address">
              <label>पत्रव्यहार पूर्ण पत्ता</label>
              <input type="text" placeholder="पूर्ण पत्ता" required />
            </div>
          </div>
          <br>
          <br>
          <div class="column">
            <div class="input-box">
              <label>तुमच्याबद्दल अधिक माहिती</label>
              <input type="text" placeholder="अधिक माहिती" required />
            </div>
          </div>
          <br>
          <div class="column">
            <div class="input-box">
              <label>पासवर्ड </label>
              <input type="password" placeholder="पासवर्ड" required />
            </div>
          </div>
          <br>
          <div class="column">
            <div class="input-box">
              <label>पासवर्ड पुन्हा प्रविष्ट करा</label>
              <input type="password" placeholder="पासवर्ड पुन्हा प्रविष्ट करा" required />
            </div>
          </div>
          <br>

          <button @click="submitRegisration()">नोंदणी करा</button>
          <v-dialog v-model="dialog" max-width="300">
            <div>
              <section class="container">

                <header></header>
                <v-card>
        <v-card-title class="headline">Registration OTP sent on your mobile Number : {{ regisration.mobileno }}</v-card-title>
        
        <v-text-field  label="Enter OTP" v-model="otp" v-validate="'required|max:10|numeric'" :counter="4" required></v-text-field>
        <!-- <v-card-text>
          Confirmation 
        </v-card-text> -->

        <v-card-actions>
          <!-- <v-spacer></v-spacer> -->

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="dialog = false"
          >
            cancle
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="submitForm"
          >
            Confirm
          </v-btn>
        </v-card-actions>
      </v-card>

              </section>
            </div>
          </v-dialog>
          

        </form>
      </section>
    </div>


  </v-container>
</template>
<script>
export default {
  name: 'userRegisration',
  data() {
    return {
      alert1:true,
      dialog:false,
      regisration:{
        mobileno:""
      },
      otp:"",
    }
  },
  created(){
    // this.$alert("Regisration sucessfull")
  },
  methods: {
    submitRegisration() {
      this.dialog = true
      // this.$router.push('/login') 
    },
    submitForm(){
          if (this.otp == "1234"){
            this.$alert("Regisration sucessfull")
            this.$router.push('/login')
          }else{
            this.$alert("Wrong OTP")
          }
    }
  }
}
</script>


