<template>
  <div>

    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card color="grey lighten-2" class="black--text">
            <v-carousel>
              <v-carousel-item v-for="(item, i) in items" :key="i" :src="item.src"></v-carousel-item>
            </v-carousel>

          </v-card>
        </v-flex>
        <v-flex xs6>
          <div class="card">
            <a class="card1" href="https://en.wikipedia.org/wiki/Amit_Shah">
              <p>Personal Profile </p>
              <p class="small"></p>
              <div class="go-corner" href="https://en.wikipedia.org/wiki/Amit_Shah">
                <div class="go-arrow">
                  →
                </div>
              </div>
            </a>
          </div>
        </v-flex>
        <v-flex xs6>
          <div class="card">
            <a class="card1" @click="urlschange('registerQuery')">
              <p>New Query </p>
              <p class="small"></p>
              <div class="go-corner">
                <div class="go-arrow">
                  →
                </div>
              </div>
            </a>
          </div>
        </v-flex>
        <v-flex xs6>
          <div class="card">
            <a class="card1" @click="urlschange('socialDashboard')">
              <p>Social Update </p>
              <p class="small"></p>
              <div class="go-corner">
                <div class="go-arrow">
                  →
                </div>
              </div>
            </a>
          </div>
        </v-flex>
        <v-flex xs6>
          <div class="card">
            <a class="card1" @click="urlschange('querySummery')">
              <p>Query Summery</p>
              <p class="small"></p>
              <div class="go-corner" >
                <div class="go-arrow">
                  →
                </div>
              </div>
            </a>
          </div>
        </v-flex>
        <!-- <v-flex xs6>
          <div class="card">
            <a class="card1" href="https://twitter.com/ajitpawarspeaks?lang=mr">
              <p>Twitter </p>
              <p class="small"></p>
              <div class="go-corner" >
                <div class="go-arrow">
                  →
                </div>
              </div>
            </a>
          </div>
        </v-flex> -->

        <!-- <v-flex xs6>
          <div class="card">
            <a class="card1" href="https://ajitpawar.org/en/home/">
              <p>web site </p>
              <p class="small"></p>
              <div class="go-corner">
                <div class="go-arrow">
                  →
                </div>
              </div>
            </a>
          </div>
        </v-flex> -->


      </v-layout>
    </v-container>
  </div>



</template>
<script>
import SvgIcon from '@jamescoyle/vue-icon';
import { mdiFacebook } from '@mdi/js';
import moment from 'moment'
import { DonutChart } from 'vue-morris'
import '../../../node_modules/vuetify/dist/vuetify.min.css'
export default {
  name: 'DashboardVoting',
  data() {
    return {
      items: [
        {
          src: 'https://www.bjp.org/files/middle-banner-images/amt-banner1.jpg'
        },
        {
          src: 'https://www.clipartmax.com/png/middle/111-1111215_the-nationalist-congress-party-while-flexing-its-muscles-rashtrawadi-congress-party-logo.png'
        },
        {
          src: 'https://cdn.ajitpawar.org/wp-content/uploads/2023/08/ajit_pawar-768x432.jpg?strip=all&lossy=1&ssl=1'
        },
        {
          src: 'https://upload.wikimedia.org/wikipedia/commons/6/63/Indian_National_Congress_hand_logo.png'
        }
      ],
      path: mdiFacebook,
      currentDate: '',
      uerInfo:'',
      donutData: [
        { label: 'Evening', value: 50.5 },
        { label: 'Morning', value: 100 }
      ]
    }
  },
  components: {
    DonutChart,
    SvgIcon
  },
  created() {
    this.$validator.localize("en", this.dictionary);
    this.$store.state.openNevigation = false
    this.uerInfo = sessionStorage.getItem('admin')
  },
  methods: {
    getCurentDate() {
      this.currentDate = new Date()
    },
    userQuery() {
      this.$router.push('create_user')
    },
    profileOk() {
      this.$router.push('profile')
    },
    urlschange(urls){
      this.$store.state.openNevigation = false
      this.$router.push(urls)
    }
  },
  mounted() {
    this.getCurentDate()
  },
  filters: {
    formatDate(value) {
      if (value) {
        return moment(String(value)).format('DD-MMM-YYYY')
      }
    }
  }
}
</script>

<style>
.card p {
  font-size: 17px;
  font-weight: 400;
  line-height: 20px;
  color: #666;
}

.card p.small {
  font-size: 14px;
}

.go-corner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 32px;
  height: 32px;
  overflow: hidden;
  top: 0;
  right: 0;
  background-color: #00838d;
  border-radius: 0 4px 0 32px;
}

.go-arrow {
  margin-top: -4px;
  margin-right: -4px;
  color: white;
  font-family: courier, sans;
}

.card1 {
  display: block;
  position: relative;
  max-width: 262px;
  background-color: #f2f8f9;
  border-radius: 4px;
  padding: 32px 24px;
  margin: 12px;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
}

.card1:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -16px;
  right: -16px;
  background: #00838d;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  transform: scale(1);
  transform-origin: 50% 50%;
  transition: transform 0.25s ease-out;
}

.card1:hover:before {
  transform: scale(21);
}

.card1:hover p {
  transition: all 0.3s ease-out;
  color: rgba(255, 255, 255, 0.8);
}

.card1:hover h3 {
  transition: all 0.3s ease-out;
  color: #fff;
}

.card2 {
  display: block;
  top: 0px;
  position: relative;
  max-width: 262px;
  background-color: #f2f8f9;
  border-radius: 4px;
  padding: 32px 24px;
  margin: 12px;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
  border: 1px solid #f2f8f9;
}

.card2:hover {
  transition: all 0.2s ease-out;
  box-shadow: 0px 4px 8px rgba(38, 38, 38, 0.2);
  top: -4px;
  border: 1px solid #ccc;
  background-color: white;
}

.card2:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -16px;
  right: -16px;
  background: #00838d;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  transform: scale(2);
  transform-origin: 50% 50%;
  transition: transform 0.15s ease-out;
}

.card2:hover:before {
  transform: scale(2.15);
}

.card3 {
  display: block;
  top: 0px;
  position: relative;
  max-width: 262px;
  background-color: #f2f8f9;
  border-radius: 4px;
  padding: 32px 24px;
  margin: 12px;
  text-decoration: none;
  overflow: hidden;
  border: 1px solid #f2f8f9;
}

.card3 .go-corner {
  opacity: 0.7;
}

.card3:hover {
  border: 1px solid #00838d;
  box-shadow: 0px 0px 999px 999px rgba(255, 255, 255, 0.5);
  z-index: 500;
}

.card3:hover p {
  color: #00838d;
}

.card3:hover .go-corner {
  transition: opactiy 0.3s linear;
  opacity: 1;
}

.card4 {
  display: block;
  top: 0px;
  position: relative;
  max-width: 262px;
  background-color: #fff;
  border-radius: 4px;
  padding: 32px 24px;
  margin: 12px;
  text-decoration: none;
  overflow: hidden;
  border: 1px solid #ccc;
}

.card4 .go-corner {
  background-color: #00838d;
  height: 100%;
  width: 16px;
  padding-right: 9px;
  border-radius: 0;
  transform: skew(6deg);
  margin-right: -36px;
  align-items: start;
  background-image: linear-gradient(-45deg, #8f479a 1%, #dc2a74 100%);
}

.card4 .go-arrow {
  transform: skew(-6deg);
  margin-left: -2px;
  margin-top: 9px;
  opacity: 0;
}

.card4:hover {
  border: 1px solid #cd3d73;
}

.card4 h3 {
  margin-top: 8px;
}

.card4:hover .go-corner {
  margin-right: -12px;
}

.card4:hover .go-arrow {
  opacity: 1;
}
</style>


