<div class="wrapper">
    <h2>Login</h2>
    <form>
      <div class="input-box">
        <input type="text" placeholder="Enter your phone"  v-model="login.userName" required>
      </div>
      <div class="input-box">
        <input type="password" placeholder="Password" v-model="login.password" required>
      </div>
      <div class="input-box button">
        <input type="Submit"  @click="LoginUser()" value="Login">
      </div>
      <div class="text">
        <h3>If you don't have an account? <a @click="redirectRegisration()">Register now</a></h3>
      </div>
    </form>
  </div>
</template>
<script>
  // import axios from 'axios'
export default {
  name: 'loginScreen',
  data () {
    return {
      login:{}
    }
  },
  created(){
    sessionStorage.setItem('admin',"no")
    
  },
  methods: {
    LoginUser () {
    // axios.post('/login',this.login)
    //     .then(response => {
    //       if(response.status == 200 && response.data.text == true){
    //         sessionStorage.setItem('authorization',response.headers.authorization);
    //          var currentUser =  this.$jwt.decode(response.headers.authorization, 'secret')
    //         this.$router.push('/dashboard')
    //         this.$toasted.show('Welcome to '+currentUser.userName,{ theme: "outline", position: "top-center", duration : 1000})
    //   }

    //     })
    //     .catch(e => {
    //     })
    if (this.login.userName == "admin" && this.login.password == "admin"){
      sessionStorage.setItem('admin',"yes")
      this.$store.state.openNevigation = false
      this.$router.push('/adminDashboardVoting')
    }else{
      sessionStorage.setItem('admin',"no")
      this.$store.state.openNevigation = false
    this.$router.push('/dashboardVoting') 
    }
    },
    redirectRegisration(){
      this.$router.push('/userRegisration')
    }
  }
}
</script>




