<template>
   <div>
    <v-toolbar flat color="white">
      <v-toolbar-title>User Details</v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md4>
                  <v-text-field v-model="editedItem.name" label="User name"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md4>
                  <!-- <v-text-field v-model="editedItem.admin" label="admin"></v-text-field> -->
                  <v-select
          :items="items"
          v-model="editedItem.admin"
          label="Solo field"
          solo
        ></v-select>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="desserts"
      class="elevation-1"
    >
      <template v-slot:items="props">
        <td>{{ props.item.name }}</td>
        <td class="text-xs-right">{{ props.item.admin }}</td>
        <td class="justify-center layout px-0">
          <v-icon
            small
            class="mr-2"
            @click="editItem(props.item)"
          >
            edit
          </v-icon>
          <v-icon
            small
            @click="deleteItem(props.item)"
          >
            delete
          </v-icon>
        </td>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </div>
    
</template>
<script>

export default {
    name: 'manageUser',
    created(){
      this.initialize()
    },
    data () {
      return {
      dialog: false,
      items: ['yes', 'no', 'suspend'],
      headers: [
        {
          text: 'User Name',
          align: 'left',
          sortable: false,
          value: 'name'
        },
        { text: 'isAdmin', value: 'admin' },
        { text: 'Actions', value: 'name', sortable: false }
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        admin: "yes"
      },
      defaultItem: {
        name: '',
        admin: "yes"
      
      }
    }
    },
computed: {
  formTitle () {
    return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
  }
},

watch: {
  dialog (val) {
    val || this.close()
  }
},
methods: {
  initialize () {
        this.desserts = [
          {
            name: 'Abhimanyu Barge',
            admin: "yes"
          },
          {
            name: 'Sumit jagtap',
            admin: "no"
          },
          {
            name: 'Rohit Jagtap',
            admin: "yes"
          },
          {
            name: 'test user',
            admin: "no"
          },
          {
            name: 'Gingerbread',
            admin: "yes"
          },
          {
            name: 'Jelly bean',
            admin: "yes"
          },
          {
            name: 'Lollipop',
            admin: "yes"
          },
          {
            name: 'Honeycomb',
            admin: "yes"
          },
          {
            name: 'Donut',
            admin: "yes"
          },
          {
            name: 'KitKat',
            admin: "yes"
          }
        ]
      },

      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      }


}
}
</script>
<style>
</style>



