<template>
   <div>
    <v-container grid-list-md >
    <v-flex xs12>
      <v-card >

        <v-list three-line >
          <template v-for="(item, index) in items" >

            <a  @click="openDialog(item)">
            <v-subheader
              v-if="item.header"
              :key="item.header"
            >
              {{ item.header }}
            </v-subheader>

            <v-divider
              v-else-if="item.divider"
              :key="index"
              :inset="item.inset"
            ></v-divider>

            <v-list-tile
              v-else
              :key="item.title"
              avatar
              @click=""
            >
              <v-list-tile-avatar>
                <img :src="item.avatar">
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.title"></v-list-tile-title>
                <v-list-tile-sub-title v-html="item.subtitle"></v-list-tile-sub-title>
                <!-- <v-list-tile-sub-title v-html="item.reverted"> </v-list-tile-sub-title> -->
              </v-list-tile-content>
            </v-list-tile>
          </a>
          </template>
        </v-list>
      </v-card>
    </v-flex>

    <!-- <v-btn
          color="red lighten-2"
          dark
          @click="openDialog()"
        >
          Click Me
        </v-btn> -->


    <div class="text-xs-center">
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card>
        <v-card-title
          class="headline grey lighten-2"
          primary-title
        >
         
          <div v-html="user.title"></div>
        </v-card-title>

        <v-card-text>
          <div v-html="user.subtitle"></div>
        </v-card-text>
        <v-divider></v-divider>
        
        <v-card-text>
          <v-select
      v-model="user.action"
      :items="action"
      :rules="[v => !!v || 'Action required']"
      label="Action "
      required
    ></v-select>


          <div>
            <v-text-field
              v-model="user.reverted"
              label="Comment"
            ></v-text-field>
            

          </div>
        </v-card-text>

        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            flat
            @click="dialog = false"
          >
           Submit
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>


  </v-container>
   </div>
   
   
    
    
    

</template>
<script>

export default {
    name: 'query',
    created(){
    },
    data () {
      return {
        user:{},
        action:[ "पूर्ण केले", "उच्च अधिकाऱ्याकडे पाठवले","पुन्हा सत्यापित करा","अवैध"],
        bio:"Revert to user",
        dialog:false,
        revertcomment:"",
        items: [
          { header: 'Today' },
          {
            userId:1,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
            title: 'Abhimanyu Barge <span style="font-family: wingdings; font-size: 200%;">&#252;</span>',
            subtitle: "<span class='text--primary'>नळाला पाणी येत नसल्याची तक्रार</span> &mdash; सोयगाव: शहरातील वार्ड क्रमांक पाचमध्ये दोन वर्षांपासून नळाला पाणी येत नाही. नागरिकांनी अनेकवेळा पाठपुरावा करून देखील नगर पंचायत दुर्लक्ष करीत आहे. वारंवार चकरा मारणाऱ्या नागरिकांना कर्मचारी उद्धट वागणूक देत असल्याचा आरोप आहे. वाल्मिकनगर भागातील २० ते २५ नळांना दोन वर्षांपासून पाणी येत नाही. जलकुंभापासून लांब असलेल्या या भागात पाणी पोहचत नसल्याने पाण्यासाठी भटकावे लागत आहे. शेजारच्या घरांना मुबलक पाणी मिळते. याबाबत अनेक वेळा नगर पंचायतमध्ये तक्रारी केल्या आहेत. मुख्याधिकारी, नगराध्यक्ष व नगरसेवक कोणीही या प्रश्नाकडे लक्ष देत नसल्याचा आरोप आहे. येथील कर्मचारी शिंदे हे आरेरावी करत असल्याची तक्रार नागरिकांनी बुधवारी मुख्याधिकारी यांच्याकडे केली. हा प्रश्न तत्काळ सोडवला नाही, तर आंदोलनाचा इशारा दिला आहे. या निवेदनावर दीपक पाटील, बापू बावस्कर, अशोक घनघाव, वसंत खेडकर, प्रभाकर घनघाव, शांताराम बावस्कर, गजानन काकडे, हरीभाऊ जाधव, सुरेश सोनवणे, सुपडू घनघाव, योगेश मानकर, अमोल बडगुजर आदींच्या सह्या आहेत.",
            reverted : "नळाला पाणी येत  सोयगाव: शहरातील वार्ड ",
            visit:true
          },
          { divider: true, inset: true },
          {
            userId:2,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/2.jpg',
            title: 'Sumit Jagtap <span class="grey--text text--lighten-1">4</span>',
            subtitle: "<span class='text--primary'>रस्ता खूप खराब आहे </span> &mdash; शहरासह जिल्ह्यातील रस्त्यांच्या दुरावस्था झाली आहे. त्यामुळे वाहनचालकांसह सर्वसामान्य हैराण झाले आहेत. यावर वस्तुस्थिती दाखवणारी वृत्तमालिका महाराष्ट्र टाइम्सने गेल्या आठवड्यात प्रकाशित केली होती. याची सार्वजनिक बांधकाम विभागाने दखल घेत या मालिकेची कात्रणे जोडत शासनाला रस्ते दुरूस्तीसाठी जादा निधीची मागणी करणारे पत्र पाठविले असल्याचे सार्वजनिक बांधकाम विभागाकडून आज सांगण्यात आले.",
            reverted : "",
            visit:false
          },
          { divider: true, inset: true },
          {
            userId:3,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/3.jpg',
            title: 'Pooja ',
            subtitle: "<span class='text--primary'>द‌िवे गुल! </span> &mdash; वसई-विरार शहर महापालिकेच्या दिवाबत्ती विभागाच्या सावळ्या गोंधळाचे प्रदर्शन सध्या शहरवासियांना होत आहे. शहरी भागात अनेकदा दिवसाही दिवे सुरू असतात. त्यामुळे ग्रामीण भागाकडे लक्ष द्यायला पालिकेकडे पुरेसे कर्मचारी नसल्याचे मत नागरिक व्यक्त करत आहेत.",
            reverted : "",
            visit:false
          },
          { divider: true, inset: true },
          {
            userId:4,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/4.jpg',
            title: 'swati shinde',
            subtitle: "<span class='text--primary'>पथदिव्यांच्या दुरुस्तीसाठी </span> &mdash; नगर- कोट्यवधी रुपये खर्च करून विविध भागांत बसवलेल्या पथदिव्यांचा बाेजवारा उडाला आहे. दिवे बंद पडल्याने चोऱ्यांचे प्रमाण वाढले आहे. महिलांच्या सुरक्षेचाही प्रश्न निर्माण झाला आहे. येत्या आठ दिवसांत पथदिव्यांची दुरुस्ती झाली नाही, तर नागरिकांसह रस्त्यावर उतरून तीव्र आंदोलन करण्याचा इशारा नगरसेविका शारदा ढवण यांनी दिला आहे. अंधाराचा गैरफायदा घेऊन चोऱ्या महिलांच्या छेडछाडीचे प्रकार घडल्यास थेट आयुक्तांवर गुन्हा दाखल करण्याची मागणी पोलिसांकडे करणार असल्याचे ढवण यांनी स्पष्ट केले.?",
            reverted : "",
            visit:false
          },
          { divider: true, inset: true },
          {
            userId:5,
            avatar: 'https://cdn.vuetifyjs.com/images/lists/5.jpg',
            title: 'Avinash kale',
            subtitle: "<span class='text--primary'>रस्ता खूप खराब आहे </span> &mdash; सावेडी उपनगरातील प्रभाग मधील नगरसेविका ढवण यांनी बंद पडलेल्या पथदिव्यांच्या दुरुस्तीसाठी मनपाकडे वेळोवेळी पाठपुरावा केला. अधिकारी कर्मचारी तक्रारीची दखल घेत नसल्याने त्यांनी दोन-तीनदा आंदोलनेही केली. दिगंबर ढवण यांनी, तर विद्युत विभागप्रमुख बाळासाहेब सावळे यांना चक्क चार तास कोंडून ठेवले होते, तरी देखील गेंड्याची कातडी असलेल्या महापालिका प्रशासनाला जाग आलेली नाही.",
            reverted : "",
            visit:false
          }
        ]
      }
    },
    methods:{
      openDialog(data){
       
        this.user = data
        if(this.user.visit == false){
          this.user.title = this.user.title+'<span style="font-family: wingdings; font-size: 200%;">&#252;</span>'
          this.user.visit = true
        }
        this.dialog = true
      }
    }
}
</script>
<style>
</style>



