import Vue from 'vue'
import Router from 'vue-router'
import Applayout from '@/common/AppLayout'
// Login page 
import BasicLogin from '@/components/login/BasicLogin'
import UserRegisration from '@/components/registration/userRegistration'
import AdminUserRegisration from '@/components/registration/adminUserRegistration'
// Dashboard 
import Dashboard from '@/components/dashboard/Dashboard'
import DashboardVoting from '@/components/dashboard/DashboardVoting'
import AdminDashboardVoting from '@/components/dashboard/AdminDashboard'
import SocialDashboard from '@/components/dashboard/SocialDashbaord'
import Query from '@/components/query/query'
import QueryDashboard from '@/components/query/QueryDashboard'
// Collection page 
import collection  from '@/components/collection/Collection'
import cattlefeed from '@/components/cattlefeed/Cattlefeed'
import Advance from '@/components/advance/Advance'
import DataEntry from '@/components/dataEntry/dataEntry'
// DMS\Client_0.1\src.vue
// User 
import RegisterQuery from '@/components/user/RegisterQuery'
import ManageUser from '@/components/user/ManageUser'
import UserList from '@/components/user/UserList'
import Profile from '@/components/user/Profile'


// Report Section 
import ReportView from'@/components/report/ReportView'
import createReport from '@/components/report/CreateReport'
import ActionReport from '@/components/report/ActionReport'
import youtubevideo from '@/components/youtube/youtubeVideo'

import QuerySummery from '@/components/report/QuerySummery'

// import event from '@/components/event'

Vue.use(Router)
export default new Router({
  mode: 'history',
  hash: false,
  routes: [{
    path: '/layout',
    name: 'layout',
    component: Applayout,
    children: [
      {
        path: '/video',
        name: 'video',
        component: youtubevideo,
        isOpenUrl:true
      },
      {
        path: '/querySummery',
        name: 'querySummery',
        component: QuerySummery,
        isOpenUrl:true
      },
      {
        path:'/actionReport',
        name: 'ActionReport',
        component: ActionReport,
        isOpenUrl:false
      },
      {
        path: '/dashboard',
        name: 'Dashboard',
        component: Dashboard,
        isOpenUrl:true
      },
      {
        path: '/dashboardVoting',
        name: 'DashboardVoting',
        component: DashboardVoting,
        isOpenUrl:true
      },
      {
        path: '/adminDashboardVoting',
        name: 'adminDashboard',
        component: AdminDashboardVoting,
        isOpenUrl:true
      },
      {
        path: '/socialDashboard',
        name: 'socialDashboard',
        component: SocialDashboard,
        isOpenUrl:true
      },  
      {
        path: '/adminUserRegistration',
        name: 'adminUserRegisration',
        component: AdminUserRegisration,
        isOpenUrl:false
      }
      ,{
        path: '/ManageUser',
        name: 'manageUser',
        component: ManageUser,
        isOpenUrl:true
      },
      {
        path: '/queryreport',
        name: 'query',
        component: Query,
        isOpenUrl:true
      },
      {
        path: '/queryDashoard',
        name: 'QueryDashboard',
        component: QueryDashboard,
        isOpenUrl:true
      },
      {
        path:"/collection",
        name:"Collection",
        component:collection,
        isOpenUrl:true
      },
      {
        path:"/cattlefeed",
        name:"Cattlefeed",
        component:cattlefeed
      },
      {
        path:"/dataEntry",
        name:"DataEntry",
        component:DataEntry
      },
      {
        path:"/advance",
        name:"Advance",
        component:Advance
      },
      {
        path: '/registerQuery',
        name: 'registerQuery',
        component: RegisterQuery
      },
      {
        path: '/userList',
        name: 'userList',
        component: UserList
      },
      {
        path: '/profile',
        name: 'Profile',
        component: Profile
      },
      {
        path:'/ReportView',
        name: 'ReportView',
        component: ReportView
      },
      {
        path:'/createReport',
        name: 'CreateReport',
        component: createReport
      }
    ]},
    {
      path: '/Login',
      name: 'loginScreen',
      component: BasicLogin
    },
    {
      path: '/userRegisration',
      name: 'userRegisration',
      component: UserRegisration
    },
    {
      path: '*',
      redirect: '/'
  },
  {
      path: '/',
      redirect: '/Login'
  },
  ]
})



