<template>
    <div  >
      <v-container grid-list-md >
            <v-layout  >
                <v-flex xs12>
                  <header> संदेश पाठवा</header>
                    <v-text-field v-model="customer.customerNameEn" label="पूर्ण नाव" :error-messages="errors.collect('name')" v-validate="'required'" data-vv-name="name" required></v-text-field>
                    <v-text-field v-model="customer.mobileNumber" label="दूरध्वनी क्रमांक" :error-messages="errors.collect('mobile')" v-validate="'required|max:10|numeric'" data-vv-name="mobile" :counter="10" required></v-text-field>
                    <v-select
      v-model="queryCity"
      :items="cities"
      :rules="[v => !!v || 'शहर आवश्यक']"
      label="शहर निवडा"
      required
    ></v-select>
     
    <div class="gender-box">
          <div class="gender-option">
            <div class="area">
              <input type="radio" id="check-city" name="area"/>
              <label for="check-city">गाव</label>
              <input type="radio" id="check-village" name="area" />
              <label for="check-village">शहर</label>
            </div>
          </div>
        </div>

    <v-select
      v-model="select"
      :items="villages"
      :rules="[v => !!v || 'गाव आवश्यक']"
      label="गाव निवडा"
      required
    ></v-select>

                    
                     <!-- <v-text-field v-model="customer.coustomerAddress" label="संपूर्ण पत्ता" :error-messages="errors.collect('coustomerAddress')" v-validate="'required'" data-vv-name="coustomerAddress" required></v-text-field> -->
                     
                     <v-select
      v-model="queryVillage"
      :items="items"
      :rules="[v => !!v || 'संदेश प्रकार आवश्यक']"
      label="संदेश प्रकार"
      required
    ></v-select>
                     
                     <v-text-field v-model="customer.coustomerVotingId" label="मतदान आयडी" :error-messages="errors.collect('votingId')" v-validate="'required'" data-vv-name="votingId" required></v-text-field>
                    
                     <v-text-field v-model="customer.coustomerQuery" label="संदेश" :error-messages="errors.collect('coustomerQuery')" v-validate="'required'" data-vv-name="coustomerQuery" required></v-text-field>
                    
                     <!-- <v-text-field v-model="customer.acountNo" label="Enter Account Number" :error-messages="errors.collect('accountno')" v-validate="'required|max:16|min:11|numeric'" data-vv-name="accountno" :counter="16" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.ifc" label="Enter IFSC Code" :error-messages="errors.collect('ifsc')" v-validate="{required:true,regex: '^[A-Za-z]{4}0[A-Z0-9a-z]{6}$'}" data-vv-name="ifsc" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.branch" label="Enter branch Name" :error-messages="errors.collect('branch')" v-validate="'required'" data-vv-name="branch" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.aadhaarNO" label="Enter aadhar Number " :error-messages="errors.collect('aadhar')" v-validate="'numeric'" data-vv-name="aadhar" :counter="12" v-on:blur="validateAdhar()" required></v-text-field> -->
                    <!-- <v-text-field v-model="customer.email" label="E-mail" :error-messages="errors.collect('email')" v-validate="'email'" data-vv-name="email"></v-text-field> -->
                   
                    <!-- <v-text-field v-model="customer.coustomerQuery" label="Enter Query" :error-messages="errors.collect('coustomerAddress')" v-validate="'required'" data-vv-name="coustomerAddress" required></v-text-field> -->
                    <br>
                    <div>
                     <input type="file" v-on:change="handleFileInputChange">
                   </div>
                   <br>
                    <v-btn block large color="info" @click="submit" >Submit </v-btn>
                </v-flex>
            </v-layout>
<v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">Do you what to send Query?</v-card-title>

        <v-card-text>
          Confirmation 
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="dialog = false"
          >
            Disagree
          </v-btn>

          <v-btn
            color="green darken-1"
            flat="flat"
            @click="submitForm"
          >
            Agree
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
      </v-container>
    </div>
</template>
<script>
import aadhaarValid from "aadhaar-validator";
  import axios from 'axios'
export default {
  $_veeValidate: {
    validator: "new"
  },
  name: "registerQuery",
  data() {
    return {
      userName: "",
      dialog:false,
      mobileNumber: null,
      accountNumber: null,
      ifscCode: null,
      aadhaarNO: null,
      bankName: "",
      email: "",
      customer: {},
      select: null,
      queryCity :null,
      queryVillage :null,
      
      items: ["गाव/शहर/परिसरासाठी विकासकामांची मागणी","गाव/शहर/परिसर पूर्ण झालेल्या कामाची माहिती","गाव/शहर/परिसर समस्या","स्पर्धा परीक्षेत मिळालेले यश"
      ,"शैक्षणिक मदत ","शैक्षणिक याच्याबद्दल माहिती", "आरोग्यासाठी मदत","शिक्षणासाठी मदत","नौकरीसाठी मदत",
      "निमंत्रण","दुःखद घटना","भेट देण्यासाठी विनंती" ,"दुःखद घटना","वैयक्तिक काम","महत्तिपूर्ण घडामोडी","इतर"],
      cities: ["Dound/दौंड ", "bhor/भोर", "indapur/इंदापूर", "baramati/बारामती"],
      villages: ["katphal/कटफळ", "khandaj/खंडज", "katewadi/काटेवाडी", "vaki/वाकी"],
      checkbox: null,
      dictionary: {
        attributes: {
          email: "E-mail Address"
        },
        custom: {
          name: {
            required: () => "Name can not be empty"
          },
          select: {
            required: "Select field is required "
          },
          mobile: {
            required: "mobile number field is required",
            max: "The mobile number field may not be greater than 10 digit",
            numeric: "Please enter Number only."
          },
          accountno: {
            numeric: "Please enter Number only.",
            required: "Account number field is required",
            max: "The Account number field may not be greater than 16 digit",
            min: "The Account number field may not be less than 11 digit"
          },
          ifsc: {
            required: "IFSC Code is required",
            regex: "Invalid IFSC Code"
          },
          aadhar: {
            required: "Please Enter Valid Aadhar number ",
            max: "The Aadhar number field may not be greater than 12 digit"
          },
          branch: {
            required: "Branch name required "
          },
           coustomerAddress: {
            required: "Coustomer Address  required "
          },
          coustomerQuery:{
            required: "Query filed required "
          },
          votingId:{
            required: "Query filed required "
          }
        }
      }
    }
  },
  created() {
    this.$validator.localize("en", this.dictionary);
    this.$store.state.openNevigation = false
  },

  methods: {
    submit() {
      this.$validator.validateAll().then((result) => {
        if(result == true){
  // axios.post(`/userRegistration`, this.customer).then(response => {
  //   // console.log(JSON.stringify(response.data.result.ops[0]));
  //   if(response.data.status === true){
  //     this.$toasted.show('Registration Success.',{ theme: "outline", position: "top-center", duration : 1500})
  //     this.customer= {}
  //     this.$validator.reset();
  //   }else{
  //     this.$toasted.show('Registration Fail.',{ theme: "outline", position: "top-center", duration : 1500})
  //   }
  //   }).catch(e => {
  //     this.errors.push(e)
  //   })
  this.dialog = true
  
    }
  })
    },
    validateAdhar() {
        if(this.aadhaarNO != null){
      var valid = aadhaarValid.isValidNumber(this.aadhaarNO)
      if(!valid){
      this.aadhaarNO = null
      }
    }
    },
    submitForm(){
      this.$alert("संदेश पाठवलेला आहे")
      this.$router.push('dashboardVoting') 
    },
    handleFileInputChange(event) {
      // Access the file through event.target.files
      const file = event.target.files[0];
      
      // Do whatever you need with the file
      console.log('Selected file:', file);
    }
  }
};
</script>

<style>

</style>



